import React, {Component} from 'react';

class SliderRange extends Component {

  render() {

    return (
      <div className="flex items-center py-2">
        <span className="mr-2 text-s text-gray-400">Volume</span>
        <input className="overflow-hidden appearance-none bg-gray-200 h-4 w-128" type="range" min="1" max="100" step="1" value={this.props.inputVol} onChange={this.props.changeVol} />
      </div>
    )
  }

}

export default SliderRange;
